@keyframes startAnimation {
	0% {
		
	}
	100% {
		transform: translateY(0);
	}
}
body {
	background-color: #292929;
}
.root {
	display: flex;
}
.root > *:nth-child(2) {
	flex-grow: 1;
	transform: translateY(100px);
	animation: startAnimation .3s ease;
	animation-fill-mode: forwards;
}
.root > *:first-child {
	flex-basis: 250px;
	flex-grow: 0;
}